.home-container {
    height: calc(100vh - 50px);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.home-img {
    width: 30vw;
    height: 30vw;
    border-radius: 50%;

    object-fit: cover;
    object-position: center top;

    margin-bottom: 2vh;
}

.home-intro {
    font-family: 'Roboto Slab', serif;
    color: #444444;
    text-align: center;
    margin-bottom: 2vh;
}

.home-name {
    font-size: calc(1rem + 1vh);
    margin-bottom: 1vh;
}

.home-university {
    font-size: calc(0.5rem + 0.75vh);
}

.home-address {
    font-size: calc(0.4rem + 0.5vh);
}

.home-icons-wrapper li {
    padding: 0.25vw;
    display: inline;
    margin-bottom: 1vh;
}

.home-icons-wrapper .home-icons {
    fill: #888888;
    height: calc(1rem + 0.75vw);
    width: calc(1rem + 0.75vw);
}

.home-icons-wrapper .home-icons:hover {
    fill: #333333;
}

.home-icons-wrapper .linkedin:hover {
    fill: #0075B5;
}

.home-icons-wrapper .github:hover {
    fill: #24292F;
}

.home-icons-wrapper .instagram:hover {
    fill: #FB3958;
}

.home-icons-wrapper .email:hover {
    fill: #D44638;
}
.about-container {
    margin-top: 50px;
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.about-intro {
    font-family: 'Roboto Slab', serif;
    color: #444444;
    margin-bottom: 1vh;

    align-items: justify;

    margin-top: 6vh;
    margin-bottom: 6vh;
    margin-left: 8vw;
    margin-right: 8vw;
}

.about-details {
    font-size: calc(0.75rem + 0.5vw);
    margin-bottom: 4vh;
    text-align: justify;
    text-justify: inter-word;
}

.about-quote {
    font-size: calc(0.5rem + 0.5vw);
    font-style: italic;

    margin-top: 4vh;
    margin-bottom: 4vh;

    text-align: right;
}
.navbar {
  background-color: #FFFFFF;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 20;
}

.nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  max-width: 1500px;
}

.main-container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
}

.nav-logo {
  color: #333333;
  align-items: center;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 1.25rem;
  flex-grow: 1;
}

.nav-menu {
  display: flex;
  list-style: none;
  text-align: center;
  margin-right: 2rem;
}

.nav-links {
  color: #666666;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  border-bottom: 3px solid transparent;
}

.fa-code {
  margin-left: 1rem;
}

.nav-item {
  line-height: 40px;
  margin-right: 1rem;
}

.nav-item:after {
  content: "";
  display: block;
  height: 3px;
  width: 0;
  background: transparent;
  transition: width 0.7s ease, background-color 0.5s ease;
}

.nav-item:hover:after {
  color: #333333;
  width: 100%;
  background: #333333;
}

.nav-item .active {
  color: #111111;
}

.nav-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    border-top: 1pxsolid #FFFFFF;
    position: absolute;
    top: 50px;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #FFFFFF;
    left: 0px;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-item .active {
    color: #333333;
    border: none;
  }

  .nav-links {
    padding: 1.5rem;
    width: 100%;
    display: table;
  }

  .nav-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    -webkit-transform: translate(-100%, 30%);
            transform: translate(-100%, 30%);
    font-size: 1.8rem;
    cursor: pointer;
    color: #333333;
  }

  .nav-icon svg {
    height: 1.25rem;
    width: 1.25rem;
  }
}
